.custom-nav {
  top: 80% !important;
  width: 70px;
  height: 70px;
  border-radius: 50%;
  border: 1px solid gray;
  justify-content: center;
  align-items: center;
  color: #fff;
}
.custom-nav::after,
.swiper .custom-nav::after {
  font-size: 20px;
}
.swiper-pagination {
  bottom: 40px !important;
}
.custom-pagi.swiper-pagination-bullet {
  background: transparent;
  border: 1px solid #fff;
  opacity: 1;
  height: 20px;
  width: 20px;
}
.custom-pagi.swiper-pagination-bullet.swiper-pagination-bullet-active {
  background: #e05809;
  border: none;
}
@media (max-width:1024px) {
  .custom-nav{
    display: none !important;
  }
}