@tailwind base;
@tailwind components;
@tailwind utilities;

body {
    @apply bg-[#141415]
}

.active {
    @apply bg-gradient-to-r from-[#F90606] to-[#CE5D0C] bg-clip-text text-transparent
}

.hero {
    background-image: url(./assets/images/Frame\ 773975428.png);
}

.swiper-button-disabled {
    @apply border-[#afa49a] text-[#a19a93]
}

.container {
    @apply px-3
}

.banner {
    background-image: linear-gradient(rgba(0, 0, 0, 0.7), rgba(0, 0, 0, 0.7)), url(./assets/images/banner.png);
    background-size: cover;
}

.h-banner {
    background-image: url('./assets/images/image\ 4.png');
    background-size: cover;
}

.rotate {
    transform: rotate(39deg) rotateY(38deg) rotateX(334deg);
    box-shadow: 41px -11px 28px 27px #cf18184d;
}

footer {
    background-image: url('./assets/images/footerBg.png');
    background-size: cover;
}

.menu-active {
    right: 0;
    /* left: 0; */
    transform: none;
}

.first {
    animation: float 6s ease-in-out infinite;
}

.second {
    animation: float 4s ease-in-out infinite;
}

.third {
    animation: float 3s ease-in-out infinite;
}

/* Loading */

.loader {
    overflow: hidden;
    position: relative;
    padding-bottom: 10px;
}

.wheel {
    fill: rgb(128, 128, 128);
    font-size: 100px;
    width: 100px;
    height: 100px;
    border-radius: 6em;
    text-align: center;
    animation: rolling 1s linear infinite;
}

#rim {
    fill: rgb(200, 134, 2);
}

#tire {
    fill: rgb(255, 255, 255);
}

.road {
    position: absolute;
    margin-top: -4px;
    width: 100%;
    height: 10px;
    top: 100%;
    border-radius: 5em;
    background-color: rgb(200, 134, 2);
    animation: road-move 2s linear infinite;
}

@keyframes rolling {
    to {
        transform: rotate(-360deg);
    }
}

@keyframes road-move {
    from {
        transform: translateX(-140%);
    }

    to {
        transform: translateX(100%);
    }
}


@keyframes float {
    0% {
        transform: translatey(0px);
    }

    50% {
        transform: translatey(-20px);
    }

    100% {
        transform: translatey(0px);
    }
}